@import url('https://fonts.googleapis.com/css2?family=Jura:wght@500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@900&display=swap');

:root {
  --main-background-color: #2c2b31;
  --main-card-backcolor: #000;
  --main-font-color: white;

  --main-blue: #61dbfb;
  --main-red: #f16059;
  --main-yellow: #ffd42d;
  --main-purple: #9358d3;

  --main-box-shadow: 0 20px 20px 10px rgba(0, 0, 0, 0.5);
}

* {
  box-sizing: border-box;
  color: var(--main-font-color);
}

html,
body {
  /* background-color: #1f1f1f; */
  background-color: var(--main-background-color);
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 10px; /* Remove scrollbar space */
  /* optional: just make scrollbar invisible; */
}

/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background-color: var(--main-card-backcolor);
  min-height: 50px;
}

.text-purple {
  color: var(--main-purple);
}
.text-red {
  color: var(--main-red);
}
.text-yellow {
  color: var(--main-yellow);
}
.text-blue {
  color: var(--main-blue);
}

/* ******************************************* Animation Classes ******************************************* */
.popup-element {
  opacity: 0;
  transition: opacity 2s ease-in;
}

.popup-element.pop {
  opacity: 1;
}
