/********************************************* Nav Bar */
nav {
  background-color: var(--main-card-backcolor);
  color: white;
  box-shadow: var(--main-box-shadow);
  width: 60px;
  height: 100vh;
  z-index: 100;

  position: fixed;
  top: 0;
  left: 0px;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: transform 0.5s ease-in-out;
}
nav.show {
  transform: translateX(0%);
}

.nav-container {
  width: 100%;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.nav-container a {
  text-decoration: none;
  font-size: 12px;

  width: 100%;
  height: 80px;

  display: flex;
  justify-content: center;
  align-items: center;

  transition: transform 0.2s ease-in-out;
}
.nav-container a:hover {
  transform: scale(1.3);
}

.nav-container img {
  width: 35px;
  height: 35px;
}

.menu-btn {
  background-color: transparent;
  color: var(--main-blue);

  border: 0;
  font-size: 3rem;
  padding: 0 10px;

  display: none;
  position: absolute;
  bottom: 10px;
  left: 10px;
  transform: translateX(100%);
}
.menu-btn:focus,
.menu-btn:active {
  outline: none;
}

@media (max-width: 1140px) {
  .menu-btn {
    display: block;
  }
  nav {
    transform: translateX(-100%);
  }
}

/* ******************************************* Intro ******************************************* */
.intro-container {
  background-color: var(--main-card-backcolor);
  box-shadow: var(--main-box-shadow);
  margin: 40px auto;

  position: relative;
  display: flex;
  justify-content: space-between;

  transition: transform 0.5s ease-in-out;
}

.intro-container .logo {
  z-index: 1;
  position: absolute;
  top: -20px;
  right: 30px;
}

.intro-container img {
  max-width: 250px;
}

.intro-container:hover {
  transform: scale(1.05);
}
.intro-container:hover img {
  -webkit-animation: glowing 2s ease-in-out;
          animation: glowing 2s ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

/* ******************************************* Intro Text ******************************************* */
.intro-text {
  /* background-color: red; */
  font-family: 'Roboto', sans-serif;
  padding: 150px 100px;
  z-index: 2;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: left;
}

.intro-text h1 {
  letter-spacing: 15px;
  font-size: 6rem;
}

.intro-text h2 {
  font-size: 5rem;
  letter-spacing: 5px;
}

.intro-text small {
  font-family: 'Jura', sans-serif;
  font-size: 1.5rem;
  letter-spacing: 2px;
}

/* ******************************************* Media Adjustment ******************************************* */
@media (max-width: 1140px) {
  .intro-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .intro-container .logo {
    position: relative;
    top: -40px;
    right: 0;
    margin: 20px auto;
  }

  .intro-text {
    padding: 100px;
    align-items: center;
  }
}

@media (max-width: 960px) {
  .intro-text {
    padding: 90px;
  }
  .intro-text h1 {
    font-size: 5rem;
    letter-spacing: 5px;
  }
  .intro-text h2 {
    font-size: 4rem;
    letter-spacing: 5px;
  }
}

@media (max-width: 720px) {
  .intro-text {
    padding: 70px;
  }
  .intro-text h1 {
    font-size: 4rem;
    letter-spacing: 5px;
  }
  .intro-text h2 {
    font-size: 3rem;
    letter-spacing: 5px;
  }
  .intro-text small {
    font-size: 1rem;
    letter-spacing: 1px;
  }
}

@media (max-width: 540px) {
  .intro-text {
    padding: 60px;
  }
  .intro-text h1 {
    font-size: 3rem;
  }
  .intro-text h2 {
    font-size: 2rem;
  }
  .intro-text small {
    font-size: 1rem;
  }
}

@media (max-width: 330px) {
  .intro-container img {
    max-width: 150px;
  }
  .intro-text {
    padding: 30px;
  }
  .intro-text h1 {
    font-size: 2rem;
  }
  .intro-text h2 {
    font-size: 1.5rem;
  }
  .intro-text small {
    font-size: 0.8rem;
  }
}

/* ******************************************* Effects & Animations ******************************************* */

@-webkit-keyframes glowing {
  0% {
    box-shadow: 0;
  }
  100% {
    box-shadow: 0 0 5px var(--main-blue), 0 0 20px var(--main-blue),
      0 0 40px #2196f3;
  }
  /* 100% {
    box-shadow: 0;
  } */
}

@keyframes glowing {
  0% {
    box-shadow: 0;
  }
  100% {
    box-shadow: 0 0 5px var(--main-blue), 0 0 20px var(--main-blue),
      0 0 40px #2196f3;
  }
  /* 100% {
    box-shadow: 0;
  } */
}

.intro-container .bg-img {
  position: absolute;

  width: 60px;
  height: 60px;
  top: 15px;

  background-position: center;
  background-repeat: no-repeat;

  -webkit-animation: fadeInOut 4s ease-in-out infinite;

          animation: fadeInOut 4s ease-in-out infinite;
}

@-webkit-keyframes fadeInOut {
  0% {
    opacity: 0;
  }
  80% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
  }
  80% {
    opacity: 0.5;
  }
  100% {
    opacity: 0;
  }
}

/* ******************************************* Scroll For More Style ******************************************* */
.intro-container .scroll-sign {
  position: absolute;
  right: 50%;
  transform: translateX(50%);
  bottom: -35px;
  -webkit-animation: pulse 1s ease infinite;
          animation: pulse 1s ease infinite;
}

@-webkit-keyframes pulse {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes pulse {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0);
  }
}

.skill-card {
  box-shadow: var(--main-box-shadow);
  background-color: var(--main-card-backcolor);
  transition: transform 0.4s ease-in-out;

  margin: 20px 0px;
  height: 500px;
  padding: 10px 15px;
}

.skill-card:hover {
  transform: scale(1.1);
}

.skill-card a {
  text-decoration: none;
  color: white;
  width: 100%;
  height: 100%;
}

/* ****************** content ****************** */
.skill-card-content {
  font-family: 'Jura', sans-serif;
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* ****************** single logo ****************** */
.skill-card-content .logo {
  flex: 2 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 200px;
}

.skill-card-content .logo img {
  width: 150px;
  height: 150px;
}

/* ****************** multi logo ****************** */
.skill-card-content .multi-logo {
  flex: 2 1;
  width: 60%;
  height: 80px;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
}

.skill-card-content .multi-logo img {
  width: 60px;
  height: 60px;
}

/* ****************** card description ****************** */
.skill-card-content .description {
  flex: 1 1;
  text-align: center;
  font-size: 1.5rem;
}

/* ****************** View Projects Text ****************** */
.skill-card small {
  visibility: hidden;
  width: 100%;
  text-align: right;
}

.skill-card:hover small {
  visibility: visible;
}

.project {
  font-family: 'Jura', sans-serif;
  background-color: var(--main-card-backcolor);
  box-shadow: var(--main-box-shadow);
  padding: 40px 40px;
  margin-top: 40px;
}

.project h1 {
  font-size: 4rem;
  color: var(--main-blue);
}

.project h3 {
  color: var(--main-blue);
}

.project a {
  color: var(--main-font-color);
}

.project-body {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.screen-shot {
  flex: 2 1;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 50px 0;
}

.screen-shot img {
  max-width: 400px;
}

.project-body .description {
  flex: 5 1;

  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 20px;
}

.project-body .description p {
  font-size: 1.2rem;
}

@media (max-width: 1140px) {
  .screen-shot img {
    max-width: 300px;
  }
}
@media (max-width: 970px) {
  .screen-shot img {
    max-width: 230px;
  }
}

@media (max-width: 720px) {
  .screen-shot img {
    max-width: 200px;
  }

  .project h1 {
    font-size: 2rem;
    color: var(--main-blue);
  }
}

.field-container {
  background-color: var(--main-card-backcolor);
  box-shadow: var(--main-box-shadow);
  width: 100%;
  padding: 40px 60px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  font-family: 'Jura', sans-serif;
}

.field-container a {
  color: var(--main-purple);
}

.field-container p {
  font-size: 1.3rem;
}

.field-container ul {
  margin: 30px 0;
}

.field-container li {
  margin: 20px 0;
}

.field-logo {
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.field-logo img {
  max-width: 200px;
}

@media (max-width: 540px) {
  .field-container {
    padding: 20px 30px;
  }
  .field-logo {
    margin-bottom: 0;
  }
  .field-logo img {
    max-width: 150px;
  }
  .field-container h1 {
    font-size: 1.5rem;
  }
  .field-container p {
    font-size: 1rem;
  }
  .field-container ul {
    margin: 0;
  }
  .field-container li {
    margin: 5px 0;
  }
}

.contact-body {
  background-color: var(--main-card-backcolor);
  box-shadow: var(--main-box-shadow);
  padding: 20px;
  margin-bottom: 20px;
  text-align: center;
  transition: transform 0.5s ease-in-out;
}

.contact-body:hover {
  transform: scale(1.1);
}

.contact-body a {
  text-decoration: none;
}

.contact-body p {
  font-size: 1.5rem;
  margin-top: 20px;
  word-wrap: break-word;
}

.contact-body .logo {
  font-size: 10rem;
  margin: 50px 0;
}

@media (max-width: 540px) {
  .contact-body .logo {
    font-size: 5rem;
  }
  .contact-body p {
    font-size: 1rem;
  }
}

:root {
  --main-background-color: #2c2b31;
  --main-card-backcolor: #000;
  --main-font-color: white;

  --main-blue: #61dbfb;
  --main-red: #f16059;
  --main-yellow: #ffd42d;
  --main-purple: #9358d3;

  --main-box-shadow: 0 20px 20px 10px rgba(0, 0, 0, 0.5);
}

* {
  box-sizing: border-box;
  color: white;
  color: var(--main-font-color);
}

html,
body {
  /* background-color: #1f1f1f; */
  background-color: #2c2b31;
  background-color: var(--main-background-color);
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  width: 10px; /* Remove scrollbar space */
  /* optional: just make scrollbar invisible; */
}

/* Optional: show position indicator in red */
::-webkit-scrollbar-thumb {
  background-color: #000;
  background-color: var(--main-card-backcolor);
  min-height: 50px;
}

.text-purple {
  color: #9358d3;
  color: var(--main-purple);
}
.text-red {
  color: #f16059;
  color: var(--main-red);
}
.text-yellow {
  color: #ffd42d;
  color: var(--main-yellow);
}
.text-blue {
  color: #61dbfb;
  color: var(--main-blue);
}

/* ******************************************* Animation Classes ******************************************* */
.popup-element {
  opacity: 0;
  transition: opacity 2s ease-in;
}

.popup-element.pop {
  opacity: 1;
}

