.project {
  font-family: 'Jura', sans-serif;
  background-color: var(--main-card-backcolor);
  box-shadow: var(--main-box-shadow);
  padding: 40px 40px;
  margin-top: 40px;
}

.project h1 {
  font-size: 4rem;
  color: var(--main-blue);
}

.project h3 {
  color: var(--main-blue);
}

.project a {
  color: var(--main-font-color);
}

.project-body {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.screen-shot {
  flex: 2;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 50px 0;
}

.screen-shot img {
  max-width: 400px;
}

.project-body .description {
  flex: 5;

  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 20px;
}

.project-body .description p {
  font-size: 1.2rem;
}

@media (max-width: 1140px) {
  .screen-shot img {
    max-width: 300px;
  }
}
@media (max-width: 970px) {
  .screen-shot img {
    max-width: 230px;
  }
}

@media (max-width: 720px) {
  .screen-shot img {
    max-width: 200px;
  }

  .project h1 {
    font-size: 2rem;
    color: var(--main-blue);
  }
}
